/* src/components/ProductItem.css */
.product-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 200px;
    margin: 1rem;
    padding: 1rem;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 10px;
  }
  
  .product-item img {
    width: 100%;
    border-radius: 10px;
    margin-bottom: 1rem;
  }
  
  .product-info {
    text-align: center;
  }
  
  .product-info h3 {
    font-size: 1.2rem;
    margin-bottom: 0.5rem;
  }
  
  .product-info p {
    font-size: 1rem;
    margin-bottom: 1rem;
  }
  
  .product-info button {
    padding: 0.5rem 1rem;
    font-size: 1rem;
    background-color: #008000;
    color: #fff;
    border: none;
    cursor: pointer;
  }
  