/* src/components/Categories.css */
.categories {
    display: flex;
    justify-content: center;
    padding: 1rem;
  }
  
  .categories button {
    margin: 0 0.5rem;
    padding: 0.5rem 1rem;
    border: none;
    background-color: #ddd;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .categories button.active {
    background-color: #008000;
    color: #fff;
  }
  