/* src/components/Header.css */
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    background-color: #fff;
    border-bottom: 1px solid #ddd;
  }
  
  .logo {
    font-size: 1.5rem;
    font-weight: bold;
  }
  
  nav ul {
    display: flex;
    list-style: none;
  }
  
  nav ul li {
    margin: 0 1rem;
    cursor: pointer;
  }
  
  .social-icons {
    display: flex;
    align-items: center;
  }
  
  .social-icons svg {
    margin-left: 1rem;
    cursor: pointer;
  }
  