.banner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    background-color: #f5f5f5;
  }
  
  .banner-text {
    width: 50%; 
    padding-right: 1rem; 
    box-sizing: border-box; 
  }
  
  .banner-text h1 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
  }
  
  .banner-text p {
    font-size: 1.2rem;
    margin-bottom: 2rem;
  }
  
  .banner-text button {
    padding: 0.5rem 1rem;
    font-size: 1rem;
    background-color: #008000;
    color: #fff;
    border: none;
    cursor: pointer;
  }
  
  .banner-image {
    width: 50%; 
    text-align: center; 
  }
  
  .banner-image img {
    max-width: 100%; 
    height: auto;
    border-radius: 10px; 
  }
  